import * as types from '../constants/ActionTypes';

const initialState = {
  loading: true,
  apiError: false
};

const status = (state = initialState, action) => {
  switch (action.type) {

    case types.IS_LOADING:
      return { loading: true, apiError: false };

    case types.DONE_LOADING:
      return { loading: false, apiError: false };
    
    case types.API_ERROR:
      return { loading: false, apiError: true };
    
    case types.DISMISS_API_ERROR:
      return { loading: false, apiError: false };

    default:
      return state;
  }
};

export default status;



